.circle {
    position: relative;
    height: 20px;
    width: 20px;
    border-radius: 10px;
    background: -webkit-radial-gradient(gray, lightgray, white, gray, lightgray, white, gray, lightgray, white, gray, lightgray, white, gray, lightgray, white, gray, lightgray, white, gray, lightgray, white );
    background: radial-gradient(gray, lightgray, white, gray, lightgray, white, gray, lightgray, white, gray, lightgray, white, gray, lightgray, white, gray, lightgray, white, gray, lightgray, white );
    border: solid 1px darkgray;
    -webkit-transition: background 1s linear, -webkit-transform 1s;
    transition: background 1s linear, -webkit-transform 1s;
    transition: transform 1s, background 1s linear;
    transition: transform 1s, background 1s linear, -webkit-transform 1s;

  }
  
  .circle2 {
    position: relative;
    top: -6px;
    left: 10px;
    height: 13px;
    width: 13px;
    border-radius: 15px;
    background: -webkit-radial-gradient(lightgray, gray 60%, lightgray);
    background: radial-gradient(lightgray, gray 60%, lightgray);
    border: solid 1px darkgray;
    -webkit-transition: background 1s linear, -webkit-transform 1s;
    transition: background 1s linear, -webkit-transform 1s;
    transition: transform 1s, background 1s linear;
    transition: transform 1s, background 1s linear, -webkit-transform 1s;

  }
  
  .switch {
    height: 22px;
    width: 42px;
    border-radius: 25px;
    /* background: radial-gradient(rgba(100, 255, 100, 1) 10%, lightgray); */
    background: -webkit-radial-gradient(white 10%, lightgray);
    background: radial-gradient(white 10%, lightgray);
    border: solid 1px gray;
    -webkit-transition: background 1s linear, -webkit-transform 1s;
    transition: background 1s linear, -webkit-transform 1s;
    transition: transform 1s, background 1s linear;
    transition: transform 1s, background 1s linear, -webkit-transform 1s;
    
  }
  
  .switch2 {
    height: 2px;
    width: 60px;
    border-radius: 50%;
    background: -webkit-radial-gradient(gray 10%, white);
    background: radial-gradient(gray 10%, white);
    /* border: solid 1px gray; */
    -webkit-transition: background 1s linear, -webkit-transform 1s;
    transition: background 1s linear, -webkit-transform 1s;
    transition: transform 1s, background 1s linear;
    transition: transform 1s, background 1s linear, -webkit-transform 1s;

  }
  
  /* .switch:hover .circle {
      transform: translate(100%, 0);
  } */
  
  .switchOn {
       -webkit-transform: translate(90%, 0);
               transform: translate(90%, 0);
       -webkit-transition: background 1s linear, -webkit-transform 1s;
       transition: background 1s linear, -webkit-transform 1s;
       transition: transform 1s, background 1s linear;
       transition: transform 1s, background 1s linear, -webkit-transform 1s;
  }
  
  .switchOnGrad {
    /* background: radial-gradient(rgba(255, 100, 100, 1) 10%, gray); */
    background: -webkit-radial-gradient(rgba(70, 100, 255, 1) 10%, white);
    background: radial-gradient(rgba(70, 100, 255, 1) 10%, white);
    -webkit-transition: background 1s linear, -webkit-transform 1s;
    transition: background 1s linear, -webkit-transform 1s;
    transition: transform 1s, background 1s linear;
    transition: transform 1s, background 1s linear, -webkit-transform 1s;

  }
  
  .switch2On {
       -webkit-transform: translate(175%, 0);
               transform: translate(175%, 0);
       background: -webkit-radial-gradient(lightblue, blue 60%, lightgray);
       background: radial-gradient(lightblue, blue 60%, lightgray);
       -webkit-transition: background 1s linear, -webkit-transform 1s;
       transition: background 1s linear, -webkit-transform 1s;
       transition: transform 1s, background 1s linear;
       transition: transform 1s, background 1s linear, -webkit-transform 1s;
  }
  
  .switch2OnGrad {
    background: -webkit-radial-gradient(blue 10%, white);
    background: radial-gradient(blue 10%, white);
    -webkit-transition: background 1s, -webkit-transform 1s;
    transition: background 1s, -webkit-transform 1s;
    transition: transform 1s, background 1s;
    transition: transform 1s, background 1s, -webkit-transform 1s;

  }

